@import url(https://fonts.googleapis.com/css2?family=Hanalei+Fill&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hanalei+Fill&display=swap);
html {
  height: 110vh;
  border: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*background-image: url("./assets/isbg4.jpg");*/
  background: linear-gradient(#33ccff,  #007399);
  background-repeat: no-repeat;
  height: 100vh;
  border: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (min-width: 2600px) {
  body {
    padding-top: 1450px !important;
    background-size: cover !important;
  }
}



.App {
  text-align: center;
  height: 100%;
  color: #f2e6d9;
  font-family: 'Hanalei Fill', cursive;
  border: none;
}
h1 {
  margin: 0px auto 20px auto;
  font-size: 50px;
  text-shadow: 5px 5px 20px 5px #4d3319;
}
h2 {
  font-size: 30px;
}
.Cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 550px;
  
  background-color: rgba(57, 38, 19, 0.5);
  /*box-shadow: 0px 0px 1px 1px #f2e6d9;*/
  border-radius: 20px;
  margin: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  opacity: 1;
}
.Ccard {
  width: 100px;
  height: 100px;
  perspective: 600px;
  margin: 15px;
  opacity: 1 !important;
  
}
.Ccard:hover {
  transform: scale(1.05);
  cursor: pointer;
}
